.project-card {
  display: flex;
  flex-direction: column;
  border: 1px solid #dddddd;
  border-radius: 8px;
  overflow: hidden;
  margin: 20px;
  background-color: #ffffff;
  transition: transform 0.2s;
  width: 500px;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.project-content {
  padding: 20px;
}

.project-content h3 {
  margin-top: 0;
  color: #24292e;
}

.project-content p {
  color: #555555;
  line-height: 1.5;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.project-tags {
  margin-top: 10px;
}

.project-tag {
  display: inline-block;
  background-color: #e0e0e0;
  color: #333333;
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 14px;
  margin-right: 5px;
  margin-bottom: 5px;
}

.project-links {
  margin-top: 15px;
}

.project-link {
  display: inline-block;
  margin-right: 10px;
  color: #61dafb;
  text-decoration: none;
  font-weight: bold;
}

.project-link:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .project-card {
    width: 90%;
  }
}
