.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #24292e;
  padding: 10px 20px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

.navbar-logo {
  color: #ffffff;
  font-size: 24px;
  font-weight: bold;
}

@media (max-width: 768px) {
  .navbar-links {
    flex-direction: column;
    align-items: center;
  }

  .navbar-links li {
    margin: 10px 0;
  }
}
