.about-me {
  padding: 100px 20px 50px 20px; /* Adjusted padding to account for fixed navbar */
  background-color: #f5f5f5;
}

.about-me h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #24292e;
}

.about-me p {
  max-width: 800px;
  margin: 0 auto 20px;
  line-height: 1.6;
  color: #333333;
  font-size: 18px;
}

.about-me a {
  color: #305CDE;
  text-decoration: none;
}

.about-me a:hover {
  text-decoration: underline;
}
